import { Map } from 'immutable';

export default function state(state = {}, action) {
  switch (action.type) {
    case "VIEW_NAVIGATION":
      console.log("REDUCER");
      console.log(action.viewDelta.center[0]);
      return state.mergeDeep(action.viewDelta);
    default:
      return state;
  }
}



